.delete-none{
  opacity: 0;
  position: relative;
  top: 18px;
  right: -8px;
  cursor: pointer;
}

.delete {
  position: relative;
  top: 18px;
  right: -8px;
  cursor: pointer;
}

.delete svg {
  filter: drop-shadow(1px 1px 1px #999)
}
.image-main-container {
  min-height: 300px;
}
.image-main{
  height: auto; 
  width: auto; 
   max-width: 300px; 
   max-height: 300px;
  padding: 10px;
  border: 1px dashed #999;
}
.image-thumb {
  max-height: 70px;
  max-width: 70px;
  padding: 5px;
  margin: 5px;
  border: 1px dashed #999;
  cursor: pointer;
}

/* Loading */
.loading-wrapper, .loading {
  display: flex;
  align-items: center;
  justify-content: center; 
  flex-direction: column;
}

.loading {
  width: 200px;
  height: 200px;
}

.loading .background {
  border-radius: 50%;
  background: #6762a6;
  border: 3px solid #c9c3e6;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.25);
  width: 200px;  
  height: 200px;
  box-sizing: border-box;
  animation: pulse-colors 2s infinite alternate linear;
}

.loading i {
  margin: 25px 5px 5px 55px;
  float: left;
  font-size: 10em !important;
  color: #fff;
  animation: pulse-icon 4s infinite alternate linear;
}


@keyframes pulse-colors {
  from {
    background: #c9c3e6;
    border: 3px solid #a29ccc;
  }
  to {
    background: #6762a6;
    border: 3px solid #c9c3e6;
  }
}

/* Animations */
.fadein {
  animation: fadein 3s;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadein-image-main {
  animation: fadein-image-main 3s;
}
@keyframes fadein-image-main {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.spinner {
  animation: spin 1.2s infinite linear;
}

.spinner svg {
  background: #f2f2f2;
  border-radius: 50%
}

@-webkit-keyframes spin {
  0%  {
    -webkit-transform: rotate(0deg)
  }
  100% {
    -webkit-transform: rotate(360deg)
  }   
}

.rolling {
  position: absolute;
  animation: move 6s ease-out;
  filter: drop-shadow(4px 4px 4px #999);
}

@-webkit-keyframes move {
  0%  {
    left: -5%;
  }
  100% {
    left: 105%;
  }   
}

@keyframes pulse-icon {
  from {
    text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.25);
  }
  to {
    text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.55);
  }
}